liiteri { 
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0.7)), to(rgba(255,255,255,0.7))), url('http://liiteri.net/wp-content/themes/liiteri-child/img/liiteri-bg.jpg') no-repeat center center fixed; 
    background-image: -webkit-linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,0.7)), url('http://liiteri.net/wp-content/themes/liiteri-child/img/liiteri-bg.jpg') no-repeat center center fixed; 
    background-image: -o-linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,0.7)), url('http://liiteri.net/wp-content/themes/liiteri-child/img/liiteri-bg.jpg') no-repeat center center fixed; 
    background-image: linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,0.7)), url('http://liiteri.net/wp-content/themes/liiteri-child/img/liiteri-bg.jpg') no-repeat center center fixed;
  background-size: cover;
}
.broken { 
  background: url('http://liiteri.net/wp-content/themes/liiteri-child/img/liiteri-bg.jpg'); 
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.circle {
  border-radius: 50%;
  width: 500px;
  height: 350px; 
  background: white;
  margin: auto;
  text-align: center
}

.circle2 {
  border-radius: 50%;
  width: 500px;
  height: 350px; 
  background: url('http://liiteri.net/wp-content/themes/liiteri-child/img/liiteri-bg.jpg'); 
  margin: auto;
  text-align: center
}

 
  


